@tailwind base;
@tailwind components;
@tailwind utilities;

.full_screen {
  background: linear-gradient(180deg, #373e44 -100%, #191b1f 100%);
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
}

.btn_component {
    width: 32%;
    height: 80%;
    border-radius: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 17px;
    font-weight: 500;
    transition: .5s;
    z-index: 5;
    background: transparent;
    position: relative;
}

.btn_component:hover::before {
  transform: scaleX(1);
}


.btn_component::before{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border-radius: 1rem;
    background: linear-gradient(to right, #1a1a1a1a, #e9e9e90d);
    z-index: -1;
    transition: transform .5s;
    transform-origin: 0 0;
    transform: scaleX(0);

}

.disable_content::before {
  content: none;
}

.btn_component::after {
  content: none;
}

.selected_button {
  transition: .5s;
  background-color: rgb(40 41 47);
  width: 32%;
  height: 80%;
  z-index: 1;
  position: absolute;
  border-radius: 18px;
}


.btn {
   
    border-radius: 1rem;
    background: linear-gradient(to right, #1a1a1a1a, #e9e9e90d);
    z-index: -1;
    transition: transform .5s;
    transform-origin: 0 0;
    transform: scaleX(0);
}


.scroll::-webkit-scrollbar {
  width: 6px;
  height: 8px;
  border-radius: 1px;
  background-color: #363c43;
}
.scroll::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: black;
  background: linear-gradient(177.32deg, #888989 5.6%, #4a4e54 93.28%);
}

.box-shadow {
  --tw-shadow: 5.67px 5.67px 3.78px 0px #00000066;
  --tw-shadow-colored: 5.67px 5.67px 3.78px 0px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000),
    var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

.line-shadow {
  --tw-shadow: 0px 3px 3.78px 0px #00000066;
  --tw-shadow-colored: 0px 3px 3.78px 0px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000),
    var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
  background: linear-gradient(
      180deg,
      rgba(40, 40, 40, 0.1) 0%,
      rgba(248, 248, 248, 0.1) 100%
    ),
    linear-gradient(0deg, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.05));
}

.arrow_btn {
  background-image: linear-gradient(139.14deg, #303439 12.4%, #161718 94.96%);
}


.arrow_btn:hover{
  --tw-shadow: 4px 5px 30px 5px #101213, -5px -3px 30px -10px #c3ddf8;
  --tw-shadow-colored: 4px 5px 30px 5px var(--tw-shadow-color), -5px -3px 30px -10px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
 
}

.add_btn:hover {
  --tw-shadow: 0 3.26px 3.26px #ffffff26 inset, 0 0 48.91px #ffffff0d inset, 9px 10px 7.1px #0006, -.5px -.5px 6.9px #ffffff40;
  --tw-shadow-colored: inset 0 3.26px 3.26px var(--tw-shadow-color), inset 0 0 48.91px var(--tw-shadow-color), 9px 10px 7.1px var(--tw-shadow-color), -.5px -.5px 6.9px var(--tw-shadow-color);
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
  
}

#image::-webkit-scrollbar {
  display: none;
}

